* {
  box-sizing: border-box;
}
body {
  background-color: #2e2e2e;
  color: #f6f6f6;
  font-family: "Noto Sans KR", sans-serif;
  font-style: 16px;
}

button {
  background-color: #00ff94;
  font-size: 1.1rem;
  color: #1f1f1f;
  font-weight: bold;
  font-family: "Noto Sans KR", sans-serif;
  border: none;
  border-radius: 8px;
  width: 100%;
  height: 40px;
}

.wrapper {
  padding: 30px 0;
  margin: 0px 30px;
  border-bottom: 1px solid #6b6b6b;
}

.title {
  font-size: 1.8rem;
  font-weight: bold;
}

.address {
  font-size: 0.8rem;
  font-weight: lighter;
}

p.ellip {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.profile-init {
  width: 45px;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#outer-wrapper {
  min-width: 320px;
  max-width: 414px;
  width: 100%;
  height: 100vh;
  max-height: 896px;
  background-color: #1f1f1f;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

#nav-wrapper {
  margin: 15px 30px 0;
}

#profile-wrapper {
  display: flex;
  flex-direction: column;
}

#profile-sub-wrapper {
  display: flex;
  margin-bottom: 30px;
}

#profile-info {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 45px;
  justify-content: space-between;
  margin-left: 15px;
}

#profile-info .name {
  font-size: 1.5rem;
  font-weight: bold;
}

#wallet-slider {
  display: flex;
  padding: 30px 30px 0;
  width: 100%;
  overflow: auto;
}

.wallet-card {
  width: 300px;
  height: 175px;
  padding: 20px;
  margin-right: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  background-color: #242424;
  border-radius: 5px;
}

.wallet-card .top {
  font-size: 1.5rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.wallet-card .top .balanceInfo .balance {
  margin-bottom: 5px;
}

.wallet-card .top .balanceInfo .balance .type {
  color: #00ff94;
  margin-right: 10px;
}

.wallet-card .top .balanceInfo .dollar {
  color: #6b6b6b;
  font-size: 1rem;
  text-align: right;
}

.wallet-card .address {
  width: 80%;
}
